<template>
  <div>
    <div class="my-search-wapper">
       <form @submit="boardSearchClick" action="#">
          <div class="table-left">
            <input @textInput="textInutCheck" maxlength="11" class="search-text" type="search" ref="input" @focus="focusHandle" v-model="currentValue" :placeholder="placeholder" >
            <div v-show="showClear" ref="clearBtn" @click="clearTextHandle" class="text-clear-div"></div>
          </div>
          <div :class="tableclass" class="table-right">
            <span class="icon-chakan iconfont" @click="lookIconClickHandle"></span>
            <slot name='addBtn'></slot>
          </div>
        </form>
    </div>
  </div>
</template>

<script>
import bus from '../../common/js/eventBus.js';

export default {
  name: "my-search",

  data() {
    return {
      visible: false,
      currentValue: this.value,
      isActivated:false,
      showClear:false
    };
  },

  components: {},

  watch: {
    currentValue(val) {
      if(val==""){
        this.showClear=false;
        bus.$emit("searchtext-empty");
      } 
      else this.showClear=true;
    },
    value(val) {
      this.currentValue = val;
    }
  },

  props: {
    value: String,
    placeholder: {
      default: "请输入手机号码 ..."
    },
    tableclass:{
      type:String
    }
  },
  activated(){
    this.isActivated=true;
  },
  deactivated(){
    this.isActivated=false;
  },
  destroyed(){
    this.isActivated=false;
  },
  mounted() {
    this.isActivated=true;
    // document.getElementsByClassName("table-right")[0].style.width=this.rightWidth;
    bus.$on("update-search-text",(textVal)=>{
      console.log("搜索框收到更新搜索文本事件..");
      if(!this.isActivated) return;
      this.currentValue=textVal;
    });
  },
  methods: {
    textInutCheck(event){
      if(!/^[0-9]*$/.test(event.data)){
        event.returnValue=false;
      }
    },
    focusHandle(event) {
      this.$emit("my-search-focus");
    },
    initiativeBlur(){
      this.$refs.input.blur();
      this.currentValue="";
    },
    lookIconClickHandle(){
      console.log("点击了查看图标");
      if(this.$route.name == "agencySalesman"||this.$route.name =="agencyTJT"||this.$route.name =="agencyShopsList"){
        this.$refs.input.focus();
        return;
      }
      this.trySearch();
    },
    boardSearchClick(event){
      this.trySearch(event);
    },
    trySearch(boardEvent=null){
      if(boardEvent) boardEvent.preventDefault();
      if(!this.currentValue||this.currentValue==""){
        return;
      }
      bus.$emit("search-Handle",this.currentValue);
      this.$refs.input.blur();
    },
    clearTextHandle(event){
      this.currentValue="";
      this.$refs.input.focus();
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>

.my-search-wapper {
  // border-bottom: 1px #ccc solid;
  form {
    width: 100%;
    display: table;
    padding: 8px 0px;
    height: 30px;

    .table-left {
      height: 100%;
      padding-left: 10px;
      position: relative;

      .search-text {
        -webkit-appearance: none;      //去掉iphone背景阴影
        border: none;
        outline: none;
        height: 100%;
        width: 100%;
        padding-left: 10px;
        padding-right: 5px;
        border: 1px solid #ebebeb; // 必须对默认的border:2px inset覆盖，要不然下面的样式也是白搭
        border-radius: 8px;
        background: #ffffff;
        display: table-cell;
        -webkit-touch-callout: none; 
      }

      .search-text[type=search]::-webkit-input-placeholder{
        color:#8a8a8a;
        font-size 14px;
      }

      .text-clear-div{
        position: absolute;
        height: 20px;
        width: 20px;
        border-radius: 50%;
        background: #dedede url('../../assets/close.png') no-repeat center;
        background-size: 14px 14px;
        right: 0px;
        top: 50%;
        transform: translate(-50%, -50%);
      }

      .search-text[type=search]::-webkit-search-cancel-button {
        -webkit-appearance: none; // 此处只是去掉默认的小×
      }

      .search-text[type=search]:focus {
        transition: border linear 0.2s, box-shadow linear 0.2s;
        -webkit-transition: border linear 0.2s, -webkit-box-shadow linear 0.1s;
        box-shadow: 0 0 3px rgba(106, 106, 106, 0.105); // 向外漫射程度1
        -webkit-box-shadow: 0 0 3px rgba(106, 106, 106, 1.5); // 向外漫射程度3
      }
    }

    .table-right {
      position relative;
      display: table-cell;
      vertical-align: middle;
      white-space:nowrap;
      transition: width 0.5s cubic-bezier(0.42,0,0.58,1) 0.05s;
      -webkit-transition: width 0.5s cubic-bezier(0.42,0,0.58,1) 0.05s;
      &.agency-width{
        width 80px;
      }
      &.shop-width{
        width 50px;
      }
      &.extend-width{
        width 50px;
      }
      
      .icon-chakan {
        padding-left: 6px;
        // font-size: 20px;
        // color: #42495C;
      }
    }
  }
}
</style>



