<template>
    <div class="agency-chain-home">
        <my-search ref="mysearch" class="my-search" :tableclass="'agency-width'" @my-search-focus="onScearchFocus">
          <div slot="addBtn" @click="addBtnClickHandle" class="icon-add-user iconfont"></div>
        </my-search>
        <add-under-popup @request-add-under="requestAddUnder" ref="underPopup"></add-under-popup>
        <keep-alive>
            <router-view @search-panel-leave="onSearchPanelLeave"></router-view>
        </keep-alive>
    </div>
</template>

<script>
import mySearch from "../../../public/mySearch.vue";
import addUnderPopup from "../../../popupBox/addUnderPopup.vue";
import { mapState } from "vuex";
import Vue from "vue";
import bus from '../../../../common/js/eventBus.js';
import { Toast } from "mint-ui";
import { MessageBox } from "../../../popupBox/messageBox/message-box.js";
import "mint-ui/lib/toast/style.css";
Vue.component(MessageBox.name, MessageBox);
Vue.component(Toast.name, Toast);


export default {
  name: "agency-chain-home",
  data() {
    return {
      addBtn: null,
      tableRight: null,
      inSearch: false,
      addManData: null,
      relieveManData:null
    };
  },
  components: {
    mySearch,
    addUnderPopup
  },
  beforeRouteEnter(to, from, next) {
    //beforeRouteEnter作用是中转 因为keepalve的tab在不是第一个标签退出去再点击回来就会出问题，
    next(vm => {
      if(to.name=="agencyChainHome"){
        vm.$router.replace({ name: "agencySalesman" });
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    MessageBox.close();
    let underPopup = this.$refs.underPopup;
    if (underPopup && underPopup.isShow) {
      underPopup.isShow = false;
    }
    next();
  },
  activated() {},
  mounted() {
    console.log("代理入口界面执行mounted方法");
  },
  computed: {
    ...mapState([
      "openid",
      "path",
      "agencySalesmanArr",
      "agencyTJTArr",
      "userid"
    ])
  },
  methods: {
    relieveCall(relieveManData){
      this.relieveManData=relieveManData;
      let job=relieveManData.role.toString()=="3"?"业务员":"美导";
      MessageBox.confirm('解除确认',{title: '解除确认',
        message: "确定将该"+job+"的身份更改为普通客户?",
        showCancelButton: true}).
        then(action => {
          this.requestRelieve();
        }).catch(response=>{
          //请求出错处理
          console.log(response);
        });
    },
    requestRelieve(){
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type:'exitsaleorhelp',
          aid:this.userid,
          sid:this.relieveManData.userid
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          let job=this.relieveManData.role.toString()=="3"?"业务员":"美导";
          let resultData = response.body;
          if (resultData.error == "ok") {
            if(this.relieveManData.role.toString()=="3") this.cancelRefreshData(this.agencySalesmanArr);
            else this.cancelRefreshData(this.agencyTJTArr);
            Toast({message: "成功解除",iconClass: "iconfont icon-tishi1",duration: 1500});
          } else {
            if(resultData.error=="-100") Toast({message: "需要先解除该"+job+"与商户的绑定关系",iconClass: "iconfont icon-tishi1",duration: 2500});
            else Toast({message: "解除失败",iconClass: "iconfont icon-tishi1",duration: 1500});
          }
        })
        .catch(response=>{
          this.$store.dispatch("toggleLoading", false);
        });
    },
    cancelRefreshData(refreshData){
      for (let i=refreshData.length-1;i>=0;i--) {
        let item = refreshData[i];
        if (item.userid == this.relieveManData.userid) {
          refreshData.splice(i,1);
          bus.$emit("cancel-refresh-upper-data");
          break;
        }
      }
    },
    addBtnClickHandle() {
      //点击了添加按钮
      MessageBox.prompt("", {
        message: "请输入对方的手机号码",
        title: "",
        inputType: "number"
      })
        .then(({ value, action }) => {
          if (action != "confirm") return;
          if (!value || value.length < 11) {
            Toast({
              message: "请输入有效的手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
            return;
          }
          var phnum = value.slice(0, 11);
          if (!/^1[34578]\d{9}$/.test(phnum)) {
            Toast({
              message: "请输入有效的手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
            return;
          }
          this.searchUnderAddedHandle(value);
        })
        .catch(response=>{
          console.log("取消了");
        });
    },
    searchUnderAddedHandle(phoneNum) {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "afubyphone",
          phone: phoneNum,
          aid: this.userid,
          role: 0
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          let resultData = response.body.arr;
          if (!resultData || resultData.length == 0) {
            Toast({
              message: "未找到该手机号码",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else {
            let waitAdd = resultData[0];
            let targetRole = waitAdd.role.toString();
            if (targetRole == "1") {

              this.addManData = waitAdd;
              this.showUnderPopup();
            } else if (targetRole == "2") {
              Toast({
                message: "该用户是店家",
                iconClass: "iconfont icon-tishi1",
                duration: 1500
              });
            } else if (targetRole == "3") {
              Toast({
                message: "该用户已经是业务员",
                iconClass: "iconfont icon-tishi1",
                duration: 1500
              });
            } else if (targetRole == "4") {
              Toast({
                message: "该用户已经是美导",
                iconClass: "iconfont icon-tishi1",
                duration: 1500
              });
            } else if (targetRole == "5") {
              Toast({
                message: "该用户是代理商",
                iconClass: "iconfont icon-tishi1",
                duration: 1500
              });
            }
          }
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          Toast({
            message: "未找到该手机号码",
            iconClass: "iconfont icon-tishi1",
            duration: 1500
          });
          this.$store.dispatch("toggleLoading", false);
        });
    },
    requestAddUnder(job) {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + "php/Route.php", {
          type: "acsorh",
          aid: this.userid,
          uid: this.addManData.userid,
          role: job
        })
        .then(response => {
          this.$store.dispatch("toggleLoading", false);
          let resultData = response.body;
          if (resultData.error == "ok") {
            var addItem = {
              userid: this.addManData.userid,
              headimgurl: this.addManData.headimgurl,
              phone: this.addManData.phone,
              username: this.addManData.username,
              role: job,
              salescommission:0
            };
            if (job.toString() == "3") {
              if (this.agencySalesmanArr)
                this.agencySalesmanArr.unshift(addItem);
            } else if (this.agencyTJTArr) this.agencyTJTArr.unshift(addItem);
            Toast({
              message: "添加成功",
              iconClass: "iconfont icon-tishi1",
              duration: 1500
            });
          } else {
            if(resultData.error.toString() == "-102"){
                Toast({
                  message: "该用户已经和商家绑定，添加失败!",
                  iconClass: "iconfont icon-tishi1",
                  duration: 2000
                });
            }else{
              Toast({
                  message: "添加失败",
                  iconClass: "iconfont icon-tishi1",
                  duration: 1500
                });
            }
            
          }
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          Toast({
            message: "添加出错!",
            iconClass: "iconfont icon-tishi1",
            duration: 1500
          });
          this.$store.dispatch("toggleLoading", false);
        });
    },
    onSearchPanelLeave() {
      console.log("离开搜索界面");
      this.$refs.mysearch.initiativeBlur();
      this.inSearch = false;
      this.searchBlurAnimate();
    },
    onScearchFocus() {
      console.log("搜索框获得光标");
      if (this.inSearch) return;
      this.inSearch = true;
      if (
        this.$route.name == "agencySalesman" ||
        this.$route.name == "agencyTJT"
      ) {
        this.$router.push({ name: "agencyChainSearch" });
        this.searchFocusAnimate();
      }
    },
    searchFocusAnimate() {
      //伸长搜索框,隐藏添加按钮，显示搜索空白区域
      if (!this.addBtn)
        this.addBtn = document.getElementsByClassName("icon-add-user")[0];
      if (!this.tableRight)
        this.tableRight = document.getElementsByClassName("table-right")[0];
      this.tableRight.classList.add("extend-width");
      this.addBtn.classList.add("extend-width");
    },
    searchBlurAnimate() {
      if (!this.tableRight.classList.contains("extend-width")) return;
      this.tableRight.classList.remove("extend-width");
      this.addBtn.classList.remove("extend-width");
    },
    showUnderPopup() {
      let underPopup = this.$refs.underPopup;
      underPopup.cellData = this.addManData;
      underPopup.isShow = true;
    },
    hideUnderPopup() {
      this.$refs.underPopup.isShow = false;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.agency-chain-home {
  height: 100%;
  box-sizing: border-box;
  flex: 1;
  // padding-top:30px;
  display: flex;
  flex-direction: column;
  overflow-x:hidden;

  .my-search {
    width: 100%;
    background-color: #42495C;
    position relative;
    z-index 22;
    .icon-chakan {
      color: #e5e7ec;
      font-size: 20px;
      font-weight 300;
    }
  }

  .icon-add-user {
    position: absolute;
    font-size: 20px;
    margin-left: 8px;
    display: inline-block;
    color: #e5e7ec;
    opacity: 1;
    transition: opacity 0.35s ease-out 0.05s;
    -webkit-transition: opacity 0.35s ease-out 0.05s;

    &.extend-width {
      opacity: 0;
    }
  }
}

.mint-msgbox {
  width: 80%;

  .mint-msgbox-content {
    min-height: 0px;
    text-align: center;

    .mint-msgbox-input {
      width: 85%;
      display: inline-block;
      padding-bottom: 6px;

      input {
        position: relative;
        left: -5px;
      }

      .mint-msgbox-errormsg {
        display: none;
      }
    }
  }
}
</style>



